.SearchBar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 80vh;
}

.SearchBar-sort-options ul {
    display: flex;
    justify-content: center;
    margin-bottom: 2.22rem;
    color: white;
}

.SearchBar-sort-options li {
    cursor: pointer;
    border-bottom: 1px solid #fff;
    padding: 0px 2rem .33rem 2rem;
    line-height: 1.13;
    text-align: center;
    font-weight: 300;
    font-size: 1rem;
    transition: color .25s;
}

.SearchBar-sort-options li:hover {
    color: #58a344;
}

.SearchBar-sort-options li.active,
.SearchBar-sort-options li.active:hover {
    border-bottom: 1px solid black;
    color: gold;
    font-weight: 600;
}

.SearchBar-fields {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 2.88rem;
    padding: 0.5rem;
}

.SearchBar-fields input {
    width: 50rem;
    font-family: 'Titillium Web', sans-serif;
    font-style: italic;
    background-color: rgba(0, 0, 0, 0);
    border-style: none;
}

 ::placeholder {
    opacity: 1;
    color: black;
}

.SearchBar-fields input:last-child {
    margin-right: 0;
}

.SearchBar-submit {
    text-align: center;
}

.SearchBar-submit a {
    border-radius: 2px;
    padding: .72rem 1.7rem;
    background-color: black;
    color: #ffffff;
    font-weight: 600;
    transition: background-color .5s;
}

.SearchBar-submit a:hover {
    cursor: pointer;
    background-color: #a7874b;
}

#foodName {
    font-size: 6rem;
    font-weight: 300;
    text-align: center;
    color: white;
}

#foodName::placeholder {
    color: white;
    opacity: 0.4;
}

#locationName {
    font-size: 3rem;
    font-weight: 100;
    text-align: center;
    color: white;
}

#locationName::placeholder {
    color: white;
    opacity: 0.4;
}

@media only screen and (max-width: 560px) {
    .SearchBar-sort-options ul {
        margin-left: 2rem;
        margin-right: 2rem;
    }
    .SearchBar-fields {
        flex-direction: column;
        align-items: center;
    }
    .SearchBar-fields input {
        width: 100%;
    }
    .SearchBar-submit {
        margin: 0 2rem;
    }
    #foodName {
        font-size: 4rem;
        font-weight: 300;
    }
    #locationName {
        font-size: 2rem;
        font-weight: 100;
    }
}